section.gallery {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 2vh;
  min-height: 85vh;
}
section.gallery img {
  margin: 1vh;
  flex: 1;
  max-height: 35vh;
  max-width: fit-content;
  border-radius: 0.75rem;
  z-index: 2;
}
section.gallery img:hover {
  align-self: center;
  filter: drop-shadow(0px 10px 10px rgba(0, 0, 0, 0.25));
}

